/* all imports should go at the top. This was from google fonts. Press the plus and get the import link */
@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Ubuntu&display=swap");

img {
  max-width: 128px;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font: 20px/1 "Ubuntu";
  background-color: #e0e0e0;
}

header h1 span {
  color: #bd9391;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

p {
  position: relative;
}

#about {
  max-width: 100%;
  text-align: center;
}

.skills-section {
  text-align: center;
  margin: 0 auto;
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  max-width: 35%;
  /* margin: 1rem; */
}

.index-header {
  display: flex;
  justify-content: space-between;
  background-color: #6cbeed;
}

.nav__list {
  padding: 1rem;
}

.nav__list .icon {
  display: none;
}

.center-title {
  align-self: center;
}

.nav__list-item {
  display: inline;
}

.home {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.portfolio-section {
  text-align: center;
  margin-top: 0;
}

.arrow {
  text-align: center;
  margin-top: 18rem;
}

.darkStyle {
  background-color: #121212;
  color: #c5c1b9;
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.tech-image {
  margin: 1rem;
}

.social-media__image {
  max-width: 2rem;
}
.social-media {
  display: block;
}

.hero-header {
  font-size: 5rem;
}

.links {
  align-self: flex-start;
}

.hero-paragraph {
  align-self: flex-start;
}

.hero-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transform: translateY(20vh);
  align-self: center;
}

.projects {
  display: flex;
  flex-direction: column;
}

.review-site {
  background-color: #6cbeed;
  display: flex;
}

.blog-site {
  background-color: #bd9391;
  display: flex;
}

.virtual-pet {
  background-color: #6cbeed;
  display: flex;
}

.highst-hospital {
  background-color: #bd9391;
  display: flex;
}
